	module.exports = {"data":{"post":{"html":"<p>Yesterday the German Interior Ministry announced the introduction of a <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,439937,00.html\" title=\"Ausweise: Innenministerium plant elektronische 'Ausländerkarte' - Politik - SPIEGEL ONLINE - Nachrichten\">personal id card for foreign residents</a>, an <em>Aufenthaltskarte</em> similar the the <em>Personalausweis</em> issued to German citizens. I'm not so hot about all my personal data on a digitally readable smart card, but it will be practical to be able to leave my passport at home when I want to <a href=\"http://www.papascott.de/archives/2005/11/04/things-an-american-cant-do-in-germany-get-a-official-id-card/\">buy a phone or rent a video</a>.</p>","fields":{"title":"","date":"29 Sep 2006","url":"https://papascott.de/2006-09-29-auslanderkarte-dont-leave-home-without-it/"}}},"pathContext":{"slug":"/2006-09-29-auslanderkarte-dont-leave-home-without-it/","prev":{"fields":{"title":"Starbucks Too Late","date":"27 Sep 2006","slug":"/2006-09-27-starbucks-too-late/"}},"next":{"fields":{"title":"Germany Invaded By Giant Insect","date":"29 Sep 2006","slug":"/2006-09-29-germany-invaded-by-giant-insect/"}}}}

